<template>
    <div v-loading="loading">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="page-toolbar clear m-t-sm">
                    <search-ipts :options="searchOptions" @submit="doSearch" v-show="searchShow"></search-ipts>
                    <div class="pull-left toolbar-candle clear">
                        <div class="app-refresh btn bg-gray1" title="" @click="refresh"><i
                            class="fa fa-refresh"></i></div>
                    </div>
                    <div class="pull-right clear">
                        <el-select v-model="status.value" placeholder="" class="m-r-sm pull-left" style="width: 80px" clearable>
                            <el-option
                                v-for="option in status.options"
                                :key="option.value"
                                :label="option.label"
                                :value="option.value">
                            </el-option>
                        </el-select>
                        <div class="pull-left m-r-sm opacity-8" title="">
                            <select-checkbox :list="showList" v-model="selectVal" style="width: 60px;"></select-checkbox>
                        </div>
                        <div class="pull-left btn opacity-8 search-btn" @click="searchShow = !searchShow"><i
                            class="fa fa-search" title=""></i></div>
                    </div>
                </div>
                <div class="page-contaoner">
                    <div class="lk-table m-t-sm">
                        <ul class="table-thead clear">
                            <li class="col-xs-24 p-n select-box">
                                <el-checkbox v-model="selectAll"></el-checkbox>
                            </li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('ID')!=-1">ID</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <!--<li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>-->
                            <!--<li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>-->
                            <!--<li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>-->
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        </ul>
                        <ul class="table-tbody clear" v-for="(item, index) in data.items">
                            <li class="col-xs-24 p-n select-box">
                                <el-checkbox :label="item.id" v-model="selectedGroup"></el-checkbox>
                            </li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('ID')!=-1" :title="item.id">{{item.trade_id}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.code">{{item.code}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.cinema_name">{{item.cinema_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.hall_name">{{item.hall_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.goods_name">{{item.goods_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.username">{{item.username}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="format(item.trade_create_time*1000)">{{format(item.trade_create_time*1000)}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="format(item.trade_start_time*1000)">{{format(item.trade_start_time*1000)}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.pay_type">{{item.pay_type}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.trade_money">{{item.trade_money}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.trade_money">{{item.trade_money}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.discount_money">{{item.discount_money}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :class="{'text-green': item.trade_status==1, 'text-red': item.trade_status==2}" :title="item.status_name">{{item.status_name}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.region_agent_name">{{item.region_agent_name}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.agent_name">{{item.agent_name}}</li>
                    <!--        <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.buyer_mobile">{{item.buyer_mobile}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.goods_num">{{item.goods_num}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.status">{{item.status}}</li>-->
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1">
                                <router-link :to="{name: 'order_detail', params: {id: item.trade_id}}" href="javascript:;" title="" class="candle-btn btn"><i
                                    class="fa fa-search-plus"></i></router-link>
                            </li>
                        </ul>
                        <ul class="table-tbody clear" v-if="data.items.length===0">
                            <li class="p-n over-omit"></li>
                        </ul>
                    </div>
                    <div class="footer clear m-t-md">
                        <div class="pull-left news-record text-lg">
                            <span v-show="data.length>0">{{offset + 1}}{{offset + data.items.length}}</span><span
                            class="text-blue">{{data.total}}</span> 
                            <el-select v-model="limit" placeholder="" style="width: 66px;">
                                <el-option
                                    v-for="(item,index) in options"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                            
                        </div>
                        <div class="pull-right">
                            <el-pagination
                                background
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-count="pages">
                            </el-pagination>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import format from '@/tools/format'
    import validate from '@/tools/validate'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader,
        },
        data: () => ({
            data: {
                curr: 1,
                total: 1,
                total_page: 1,
                items: []
            },
            teamOptions: [],
            loading: false,
            modalLoading: false,
            selectVal: ['ID', '', '', '', '', '','', '','','','','','','', ''],
            showList: ['ID', '', '', '', '', '','', '','','','','','','', ''],
            options: [10, 25, 50],   //
            searchShow: false,   //
            selectAll: false,    //
            selectedGroup: [], // 
            limit: 10,
            page: 1,
            type: '',
            detailModal: false, // 
            editModal: false, //
            //
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'order_list'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'order_list'
                    }
                }
            },
            //
            searchOptions: [
                {
                    type: 'searchSelect',
                    name: '',
                    value: '',
                    options: []
                },
                {
                    type: 'searchSelect',
                    name: '',
                    value: '',
                    options: []
                },
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: [
                        {
                            value: 'wechat',
                            label: ''
                        },
                        {
                            value: 'alipay',
                            label: ''
                        },
                    ]
                },
                {
                    type: 'time1',
                    name: '',
                    value: ''
                },
                {
                    type: 'time1',
                    name: '',
                    value: ''
                },
            ],
            status: {
                value: '',
                options: [
                    {
                        value: 1,
                        label: ''
                    },
                    {
                        value: 2,
                        label: ''
                    },
                    {
                        value: 3,
                        label: ''
                    },
                    {
                        value: 4,
                        label: ''
                    },
                    {
                        value: 5,
                        label: ''
                    },
                    {
                        value: 6,
                        label: ''
                    },
                    {
                        value: 7,
                        label: ''
                    },
                ]
            }
        }),
        computed: {
            //
            pages () {
                return this.data.pageSize || 1
            },
            offset () {
                return (this.page - 1) * this.limit
            }
        },
        methods: {
            //
            getList () {
                this.loading = true
                this.$http.post(api.order.list, {
                    agent_id: this.searchOptions[0].value,
                    cinema_id: this.searchOptions[1].value,
                    pay_type: this.searchOptions[2].value,
                    order_before_time: this.searchOptions[3].value,
                    order_after_time: this.searchOptions[4].value,
                    status: this.status.value,
                    page: this.page,
                    limit: this.limit
                }).then(res => {
                    this.selectedGroup = []
                    let that = this
                    setTimeout(function () {
                        that.loading = false
                    }, 500)
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = {
                            limit: 10,
                            page: 1,
                            pageSize: 1,
                            total: 1,
                            items: []
                        }
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            // 
            getGroupList () {
                this.$http.post(api.agent.getAllList).then(res => {
                    if (res.data.code === 1) {
                        this.agentList = res.data.data
                        this.searchOptions[0].options = res.data.data
                    } else {
                        this.agentList = []
                        this.searchOptions[0].options = []
                    }
                })
            },
            getCinema () {
                this.$http.post(api.cinema.getAllList).then(res => {
                    if (res.data.code === 1) {
                        this.searchOptions[1].options = res.data.data.map(val => {
                            return {
                                label: val.name,
                                value: val.id
                            }
                        })
                    } else {
                        this.searchOptions[1].options = []
                    }
                })
            },
            refresh () {
                this.getList()
            },
            // 
            doSearch (data) {
                this.page = 1
                this.searchOptions = data
                this.getList()
            },
            // 
            addPage () {
                if (this.page < this.pages) this.page += 1
            },
            // 
            delPage () {
                if (this.page > 1) this.page -= 1
            },
            format: format
        },
        created () {
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
            this.getList()
        },
        watch: {
            page (val) {
                this.$router.replace({name: 'order_list', query: {page: val}})
                this.getList()
            },
            limit (val) {
                this.getList()
            },
            searchShow (val) {
                if (val) {
                    this.getGroupList()
                    this.getCinema()
                }
            },
            'status.value' (val) {
                this.getList()
            }
        }
    }
</script>
<style>

</style>




