<template>
    <div class="">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="m-t-sm ">
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="ID" prop="name" class="search-item col-xs-4 col-sm-4 col-md-2 col-lg-4 p-l-n">
                            <el-input v-model="ruleForm.name" style="max-width: 202px;"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="region" class="search-item col-xs-4 col-sm-4 col-md-2 col-lg-4 p-l-n">
                            <el-select v-model="ruleForm.region" placeholder="">
                                <el-option label="" value="shanghai"></el-option>
                                <el-option label="" value="beijing"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="region" class="search-item col-xs-4 col-sm-4 col-md-2 col-lg-4 p-l-n">
                            <el-select v-model="ruleForm.region" placeholder="">
                                <el-option label="" value="shanghai"></el-option>
                                <el-option label="" value="beijing"></el-option>
                            </el-select>
                        </el-form-item>



                        <el-form-item label="" prop="region" class="search-item col-xs-4 col-sm-4 col-md-4 col-lg-4  p-l-n">
                            <el-select v-model="ruleForm.region" placeholder="">
                                <el-option label="" value="shanghai"></el-option>
                                <el-option label="" value="beijing"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="name" class="search-item col-xs-4 col-sm-4 col-md-4 col-lg-4  p-l-n">
                            <el-input v-model="ruleForm.name"  style="max-width: 202px;"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="name" class="search-item col-xs-4 col-sm-4 col-md-4 col-lg-4  p-l-n">
                            <el-input v-model="ruleForm.name"  style="max-width: 202px;"></el-input>
                        </el-form-item>




                        <el-form-item label="" prop="name" class="search-item col-xs-4 col-sm-4 col-md-4 col-lg-2  p-l-n">
                            <el-input v-model="ruleForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="name" class="search-item col-xs-4 col-sm-4 col-md-4 col-lg-3  p-l-n">
                            <el-input v-model="ruleForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="name" class="search-item col-xs-4 col-sm-4 col-md-4 col-lg-4  p-l-n">
                            <el-input v-model="ruleForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="name" class="search-item col-xs-4 col-sm-4 col-md-4 col-lg-3  p-l-n">
                            <el-input v-model="ruleForm.name"></el-input>
                        </el-form-item>




                        <el-form-item label="" prop="type">
                            <el-checkbox-group v-model="ruleForm.type">
                                <el-checkbox label="" name="type"></el-checkbox>
                                <el-checkbox label="" name="type"></el-checkbox>
                                <el-checkbox label="" name="type"></el-checkbox>
                                <el-checkbox label="" name="type"></el-checkbox>
                                <el-checkbox label="" name="type"></el-checkbox>
                                <el-checkbox label="" name="type"></el-checkbox>
                                <el-checkbox label="" name="type"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                        <el-form-item label="" prop="desc">
                            <el-input type="textarea" v-model="ruleForm.desc"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                            <el-button @click="resetForm('ruleForm')"></el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    export default {
        data: () => ({
            ruleForm: {
                name: '',
                region: '',
                type: [],
                desc: ''
            },
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '', trigger: 'change' }
                ],
                type: [
                    { type: 'array', required: true, message: '', trigger: 'change' }
                ],
                desc: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'movieHall_list'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'movieHall_addcinemas'
                    }
                }
            }
        }),
        components: {
            SubHeader
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        },
        created () {
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
        }
    }
</script>
<style scoped>
</style>



