<template>
    <!---->
    <div v-loading="loading">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="page-toolbar clear m-t-sm">
                    <search-ipts :options="searchOptions" @submit="doSearch" v-show="searchShow"></search-ipts>
                    <div class="pull-left toolbar-candle clear">
                        <div class="app-refresh btn bg-gray1" title="" @click="refresh"><i
                            class="fa fa-refresh"></i></div>
                    </div>
                    <div class="pull-right clear">
                        <div class="pull-left m-r-sm opacity-8" title="">
                            <select-checkbox :list="showList" v-model="selectVal" style="width: 60px;"></select-checkbox>
                        </div>
                        <div class="pull-left btn opacity-8 search-btn" @click="searchShow = !searchShow"><i
                            class="fa fa-search" title=""></i></div>
                    </div>
                </div>
                <div class="page-contaoner">
                    <div class="lk-table m-t-sm">
                        <ul class="table-thead clear">
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('ID')!=-1">ID</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        </ul>
                        <ul class="table-tbody clear" v-for="(item, index) in data.items">
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('ID')!=-1" :title="item.id">{{item.id}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.createtime">{{item.createtime}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.film_name">{{item.film_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.hall_name">{{item.hall_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.hall_code">{{item.hall_code}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.order_money">{{item.order_money}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.cinema_income" >{{item.cinema_income}}</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"  :title="item.region_agent_income">{{item.region_agent_income}}</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"  :title="item.agent_income">{{item.agent_income}}</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"  :title="item.cinema_name">{{item.cinema_name}}</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"  :title="item.regionagent_name">{{item.regionagent_name}}</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"  :title="item.agent_name">{{item.agent_name}}</li>
                        </ul>
                        <ul class="table-tbody clear" v-if="data.items.length===0">
                            <li class="p-n over-omit"></li>
                        </ul>
                    </div>
                    <div class="footer clear m-t-md">
                        <div class="pull-left news-record text-lg">
                            <span v-show="data.items.length>0">{{offset + 1}}{{offset + data.items.length}}</span><span
                            class="text-blue">{{data.total}}</span> 
                            <el-select v-model="limit" placeholder="" style="width: 66px;">
                                <el-option
                                    v-for="(item,index) in options"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                            
                        </div>
                        <div class="pull-right">
                            <el-pagination
                                background
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-count="pages">
                            </el-pagination>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import format from '@/tools/format'
    import validate from '@/tools/validate'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader,
        },
        data: () => ({
            data: {
                limit: 10,
                page: 1,
                pageSize: 1,
                total: 1,
                items: []
            },
            loading: false,
            selectVal: ['ID', '', '', '', '', '', '', '','','','',''],
            showList: ['ID', '', '', '', '', '', '', '','','','',''],
            options: [10, 25, 50],   //
            searchShow: false,   //
            selectAll: false,    //
            limit: 10,
            page: 1,
            //
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'financial_list'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'financial_detail'
                    }
                }
            },
            //
            searchOptions: [
                {
                    type: 'time',
                    name: '',
                    value: ''
                }
            ],
        }),
        computed: {
            //
            pages () {
                return this.data.pageSize || 1
            },
            offset () {
                return (this.page - 1) * this.limit
            }
        },
        methods: {
            //
            getList () {
                this.loading = true
                this.$http.get(api.income.detail, {
                    params:{
                        time: this.searchOptions[0].value || this.$route.query.time,
                        id: this.$route.query.id,
                        page: this.page,
                        limit: this.limit
                    }
                }).then(res => {
                    let that = this
                    setTimeout(function () {
                        that.loading = false
                    }, 500)
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = {}
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            //
            refresh () {
                this.getList()    //
            },
            // 
            doSearch (data) {
                this.page = 1
                this.searchOptions = data
                this.getList()
            },
            // 
            addPage () {
                if (this.page < this.pages) this.page += 1
            },
            // 
            delPage () {
                if (this.page > 1) this.page -= 1
            },
            format: format
        },
        created () {
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
            this.getList()
        },
        watch: {
            page (val) {
                this.$router.replace({name: 'financial_detail', query: {page: val}})
                this.getList()
            },
            limit (val) {
                this.getList()
            },
        }
    }
</script>
<style>

</style>




